/**
 * ADS-008: Size Presets
 * Standard ad sizes for various platforms and use cases
 */

/**
 * Ad dimensions interface
 */
export interface AdSize {
  /** Unique identifier for the size */
  id: string;
  /** Display name */
  name: string;
  /** Width in pixels */
  width: number;
  /** Height in pixels */
  height: number;
  /** Aspect ratio (e.g., "1:1", "16:9", "4:5") */
  aspectRatio: string;
  /** Platform this size is optimized for */
  platform: string;
  /** Category (e.g., "social", "display", "video") */
  category: AdSizeCategory;
  /** Description of common use cases */
  description?: string;
  /** Tags for filtering */
  tags?: string[];
  /** Whether this is a common/popular size */
  recommended?: boolean;
}

/**
 * Ad size categories
 */
export type AdSizeCategory =
  | 'social'
  | 'display'
  | 'video'
  | 'print'
  | 'email'
  | 'custom';

/**
 * Platform-specific size groups
 */
export type AdPlatform =
  | 'Facebook'
  | 'Instagram'
  | 'Twitter'
  | 'LinkedIn'
  | 'Pinterest'
  | 'TikTok'
  | 'YouTube'
  | 'Google Display'
  | 'IAB Standard'
  | 'Custom';

