/**
 * Get all size presets
 */
export function getAllSizes(): AdSize[] {
  return AD_SIZE_PRESETS;
}

/**
 * Get recommended sizes (most commonly used)
 */
export function getRecommendedSizes(): AdSize[] {
  return AD_SIZE_PRESETS.filter((size) => size.recommended);
}

/**
 * Get sizes by platform
 */
export function getSizesByPlatform(platform: AdPlatform): AdSize[] {
  return AD_SIZE_PRESETS.filter((size) => size.platform === platform);
}

/**
 * Get sizes by category
 */
export function getSizesByCategory(category: AdSizeCategory): AdSize[] {
  return AD_SIZE_PRESETS.filter((size) => size.category === category);
}

/**
 * Get size by ID
 */
export function getSizeById(id: string): AdSize | undefined {
  return AD_SIZE_PRESETS.find((size) => size.id === id);
}

/**
 * Get sizes by tag
 */
export function getSizesByTag(tag: string): AdSize[] {
  return AD_SIZE_PRESETS.filter((size) => size.tags?.includes(tag));
}

/**
 * Get sizes by aspect ratio
 */
export function getSizesByAspectRatio(aspectRatio: string): AdSize[] {
  return AD_SIZE_PRESETS.filter((size) => size.aspectRatio === aspectRatio);
}

/**
 * Find closest matching size for given dimensions
 */
export function findClosestSize(width: number, height: number): AdSize | null {
  if (AD_SIZE_PRESETS.length === 0) return null;

  let closest = AD_SIZE_PRESETS[0];
  let minDistance = Math.sqrt(
    Math.pow(width - closest.width, 2) + Math.pow(height - closest.height, 2)
  );

  for (const size of AD_SIZE_PRESETS) {
    const distance = Math.sqrt(
      Math.pow(width - size.width, 2) + Math.pow(height - size.height, 2)
    );
    if (distance < minDistance) {
      minDistance = distance;
      closest = size;
    }
  }

  return closest;
}

/**
 * Get list of all unique platforms
 */
export function getAllPlatforms(): string[] {
  const platforms = new Set(AD_SIZE_PRESETS.map((size) => size.platform));
  return Array.from(platforms).sort();
}

/**
 * Get list of all unique categories
 */
export function getAllCategories(): AdSizeCategory[] {
  const categories = new Set(AD_SIZE_PRESETS.map((size) => size.category));
  return Array.from(categories).sort();
}

/**
 * Convert AdSize to legacy AdDimensions format for backwards compatibility
 */
export function toAdDimensions(size: AdSize) {
  return {
    width: size.width,
    height: size.height,
    name: size.name,
    platform: size.platform,
  };
}

/**
 * Create a map for quick lookup by size key
 */
export const AD_SIZES_MAP = AD_SIZE_PRESETS.reduce((acc, size) => {
  acc[size.id] = size;
  return acc;
}, {} as Record<string, AdSize>);

/**
 * Export legacy format for backwards compatibility with existing code
 */
export const AD_SIZES_LEGACY = AD_SIZE_PRESETS.reduce((acc, size) => {
  // Convert ID to uppercase with underscores (e.g., 'instagram-square' -> 'INSTAGRAM_SQUARE')
  const key = size.id.toUpperCase().replace(/-/g, '_');
  acc[key] = toAdDimensions(size);
  return acc;
}, {} as Record<string, { width: number; height: number; name: string; platform?: string }>);
